.meixbox{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.441);
}
.addProd{
    border: 3px solid rgba(255, 255, 255, 0.147);
    margin-bottom: 6rem;
    height: 30rem;
    width: 25rem;
    background-color: var(--bg-clr);
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.507);
    border-radius: 10px;
}
.toplist{
    background-color:var(--bg-clr);
    width: 100%;
    height: 6%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.botlist{
    width: 100%;
    height: 96%;
    background-color: rgba(0, 0, 0, 0);
}
.text23{
    padding-left: 3rem;
    width: 87%;
    font-weight: 500;
    background-color: transparent;
    color: var(--h3txt-clr);
}
.gol{
    cursor: pointer;
    background-color: rgba(127, 255, 212, 0);
    width: 15%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.gol1{
    background-color: rgba(255, 0, 0, 0.863);
    border-radius: 50%;
    width: 15px;
    height: 15px;
}
.gol2{
    background-color: rgb(255, 187, 0);
    border-radius: 50%;
    width: 15px;
    height: 15px;
}
.gol3{
    background-color: rgba(81, 255, 0, 0.87);
    border-radius: 50%;
    width: 15px;
    height: 15px;
}

@media (max-width: 600px) {
